import $ from 'jquery';

const promoSlider = $('.slider--promo').slick({
	dots: true
});

const priceSlider = $('.slider--pricelist .slider__inner').slick({
	dots: true,
	arrow: false,
	centerMode: true,
	centerPadding: '20%',
	slidesToShow: 1,
	slidesToScroll: 1,
	infinite: false,
	prevArrow: '.controls__arrow--prev',
	nextArrow: '.controls__arrow--next',
	mobileFirst: true,
	responsive: [
		{
			breakpoint: 992,
			settings: {
				centerMode: false,
				slidesToShow: 3,
				slidesToScroll: 3,
				dots: false,
				arrow: true
			}
		}
	]
});

const advantagesSlider = $('.slider--advantages').slick({
	dots: false,
	arrow: false,
	centerMode: true,
	centerPadding: '80px',
	slidesToShow: 1,
	slidesToScroll: 1,
	infinite: false,
	mobileFirst: true,
	responsive: [
		{
			breakpoint: 992,
			settings: {
				centerMode: false,
				slidesToShow: 4,
				slidesToScroll: 1,
				dots: false,
				arrow: false
			}
		}
	]
});

priceSlider.on('beforeChange', (event, slick, currentSlide, nextSlide) => {
	const node = $('.slider__background');

	node.css('transform', `translateX(-${100 / 6 * nextSlide}%)`);
});

const removeSlide = (slider, id) => {
	slider.slick('slickRemove', id);
};

export { promoSlider, priceSlider, advantagesSlider, removeSlide };