import $ from 'jquery';

const hole = $('.stage__hole svg');
const coverLeft = hole.find('#Cover-L');
const coverRight = hole.find('#Cover-R');
const bottle = $('.stage__bottle');

const moveBottle = nextIndex => {
	if (nextIndex === 2) {
		bottle.addClass('stage__bottle--moved');

		setTimeout(() => {
			bottle.hide();
		}, 300);
	} else {
		bottle.show();
		bottle.removeClass('stage__bottle--moved');
	}
};

const toggleHole = nextIndex => {
	const leftShadow = coverLeft.find('.cover__shadow');
	const rightShadow = coverRight.find('.cover__shadow');

	if (nextIndex === 2) {
		leftShadow.css('opacity', 0);
		rightShadow.css('opacity', 0);
		coverLeft.css('transform', 'translate(59px, 17px)');
		coverRight.css('transform', 'translate(132px, 17px)');
	} else {
		leftShadow.css('opacity', 1);
		rightShadow.css('opacity', 1);
		coverLeft.css('transform', 'translate(0, 0)');
		coverRight.css('transform', 'translate(192px, 0)');
	};
};

export { moveBottle, toggleHole };