const amountNode = document.querySelector('.amount');
let amount = localStorage.amount || 23829;

const addSpaceToNum = num => {
    return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
};

const getRandomInt = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
};

// Рандомный интервал //
const count = () => {
	const interval = getRandomInt(10, 20) * 1000;

	setTimeout(() => {
		// Вычитаем рандомное число //
		amount = amount - getRandomInt(1, 3);

		localStorage.setItem('amount', amount);
		amountNode.textContent = addSpaceToNum(amount);
		
		count();
	}, interval);
};

amountNode.textContent = addSpaceToNum(amount);
count();
